import './App.css';
import { Button, Navbar, Container, Nav} from 'react-bootstrap';
import bgimg from './img/bg.png' ;
import { useState } from 'react';
import data from './data.js' ;
import {Routes, Route, Link, useNavigate, Outlet, useParams} from 'react-router-dom' ;
import Detail from './routes/detail.js' ;
import axios from 'axios' ;


function App() {
  let [shoes, setShoes] = useState(data) ;
  let navigate = useNavigate() ;
  let [btnCount, setBtnCount] = useState(0) ;
  return (
    <div className="App">
      <Navbar bg="dark" variant="dark">
        <Container>
        <Navbar.Brand href="#home">Navbar</Navbar.Brand>
        <Nav className="me-auto">
          {/* <Link className="topMenu" to="/"></Link>
          <Link className="topMenu" to="/detail"></Link> */}
          {/* navigate(1) , navigate(-1)  1, 1 */}
          <Nav.Link onClick={()=>{navigate('/')}} >Home</Nav.Link>
          <Nav.Link onClick={()=>{navigate('/detail')}}>Cart</Nav.Link>
          <Nav.Link onClick={()=>{navigate('/about')}}>About</Nav.Link>
          <Nav.Link onClick={()=>{navigate('/event')}}>Event</Nav.Link>
          <Nav.Link onClick={()=>{navigate('/etc')}}>ETC</Nav.Link>
        </Nav>
        </Container>
      </Navbar>
      
      <Routes>
        <Route path="/" element={
          <>
            <div className="mainBg" style={{backgroundImage : 'url('+bgimg+')'}}></div>
            <div className="container">
              <div className="row">
              {
                shoes.map(function(a,i){
                  return(
                    <Item shoes={shoes[i]} key={i}/>
                  )
                })
              }
              </div>
            </div>

            <button onClick={()=>{
              //UI
             
              let copyBtnCount = btnCount ;
              copyBtnCount = copyBtnCount+1 ;
              setBtnCount(copyBtnCount) ;
              let filename = '' ;
              if(copyBtnCount=='1')
                {filename = 'https://codingapple1.github.io/shop/data2.json'}
              else if(copyBtnCount=='2')
                {filename = 'https://codingapple1.github.io/shop/data3.json'}
              else{
                filename = '' ; 
                alert('  .')
              } 
              
              if(filename != ''){
              axios.get(filename)
              .then((result)=>{
                let copyShoes = [...shoes] ; //,...result.data
                result.data.forEach((a,i)=>{
                  var no = parseInt(result.data[i].id)+1
                  result.data[i].img = '/img/shoes'+no+'.jpg' ;
                  copyShoes.push(result.data[i]) ;
                });
                setShoes(copyShoes) ;
              }).catch(()=>{
                alert('') ;
              })
            }
            //UI .............
            axios.post('URL',{name:"kim"})  //sample 
            Promise.add([axios.post('URL',{}),axios.post('URL',{})])
            .then(()=>{

            })
            fetch('https://codingapple1.github.io/shop/data2.json')
            .then(rs=>rs.json())  //fetch   
            .then(data=>{})   //  .
            }}></button>           
          </>
          }>
        </Route>
        <Route path="/detail/:id" element={
            <Detail shoes={shoes}/>
          }>
        </Route>
        <Route path="/about" element={<div><About/></div>}>
          <Route path="member" element={<div>-</div>}></Route>
          <Route path="location" element={<div>-</div>}></Route>
        </Route>

        <Route path="*" element={   //404
          <div> .</div>
          }>
        </Route>
        <Route path="/event" element={<div><Event/></div>}>
          <Route path="one" element={<div>   </div>}></Route>
          <Route path="two" element={<div> </div>}></Route>
        </Route>

        <Route path="/etc/:id" element={
                <EtcPage/>
        }>

        </Route>
      </Routes>
      
    </div>
  );
}

function ShowShoes(props){
  return(
    <div className="container">
      <div className="row">
      {
        props.shoes.map(function(a,i){
          return(
            <Item shoes={props.shoes[i]} key={i}/>
          )
        })
      }
      </div>
    </div>
  )
}
function Item(props){
  return(
    <div className='col-md-4' style={{float : 'left'}} >
      {/* onClick={()=>{navigate('/detail')}} */}
      <img src={process.env.PUBLIC_URL+props.shoes.img} width="80%"></img>
      <h4>{props.shoes.title}</h4>
      <p>{props.shoes.content}</p>
      <p>{props.shoes.price}</p>
      {/* <Routes>
        <Route path="/detail" element={
              <Detail shoes={props.shoes}/>
            }>
        </Route>
      </Routes> */}
    </div>
  )
}
//<DetailPage shoes = {shoes}/>
function DetailPage(props){
  return(
    <div className='container'>
      <div className='row'>
        {
          props.shoes.map(function(a,i){
            return(
              <Item shoes={props.shoes[i]}></Item>
            )
          }
        )}
      </div>
    </div>  
  )
}

function About(){
  return(
    <div>
      <h4></h4>
      <Outlet></Outlet>
    </div>
  ) 
}

function Event(){
  return(
    <div>
      <h4> </h4>
      <Outlet></Outlet>
    </div>
  )
}

function EtcPage(){
  let {id} = useParams() ;
  return(
    <div>
      <p>etc {id} </p>
    </div>
  )
}

export default App;
