import { cleanup } from "@testing-library/react";
import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import styled from 'styled-components' ;
import { Nav} from 'react-bootstrap';
// let MyBtn = styled.button`
//     background : ${props=>props.bg};
//     color : ${ props=>props.bg == 'blue' ? 'white' : 'black'} ;
//     padding : 10px ;
//     border-radius : 5px ;
//     border : none ;
// ` ;
// let MyBox = styled.div`
//     background : gray;
//     padding : 20px ;
// ` ;
// let NewBtn = styled.button(MyBtn) ;

function Detail(props){
    let [count, setCount] = useState(0) ;
    let {id} = useParams() ;
    let findItem = props.shoes.find(function(x){
        return x.id == id ;
    })
    let [alert, setAlert] = useState(true) ;
    let [tab, setTab] = useState(0) ;
    useEffect(()=>{
        //    .
        let myTimer = setTimeout(function(){
            {alert == true
                ?setAlert(alert=false) 
                :setAlert(alert=true)
            }
        },2000)
        return()=>{
            
            clearTimeout(myTimer) ; //Timer
        } //useEffect   
    },[count])  //count  count  
    // useEffect(()=>{
    //     //    .
    //     setTimeout(function(){
    //         {alert == true
    //             ?setAlert(alert=false) 
    //             :setAlert(alert=true)
    //         }
    //     },2000)
    // },[])  //[]        
    return(
        <div className="container">
            {
                alert == true 
                ? <div className="alert alert-warning">
                  2  
                </div>
                : null
            }
            {/* <MyBox>
             <MyBtn bg="blue"></MyBtn>
             <MyBtn bg="yellow"></MyBtn>
            </MyBox> */}
            {count}<button onClick={()=>{
                setCount(count+1)
            }}></button>
            <div className="row">
                <div className="col-md-6">
                    <img src={process.env.PUBLIC_URL+findItem.img} width ="80%"></img>
                </div>
                <div className="col-md-6">
                    <h4 className="pt-5">{findItem.title}</h4>
                    <p>{findItem.content}</p>
                    <p>{findItem.price}</p>
                    <button className="btn btn-danger"></button>
                </div>
            </div>

            <Nav variant="tabs" defaultActiveKey="link0">
                <Nav.Item>
                    <Nav.Link onClick={()=>{
                        setTab(0) ;
                    }}eventKey="link0">0</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link onClick={()=>{
                        setTab(1) ;
                    }}eventKey="link1">1</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link onClick={()=>{
                        setTab(2) ;
                    }}eventKey="link2">2</Nav.Link>
                </Nav.Item>
            </Nav>
            <ShowTab tab = {tab}/>
        </div>
    )
}

function ShowTab({tab}){
    let [fade, setFade] = useState('') ;
    useEffect(()=>{
        let timer = setTimeout(()=>{setFade('end') },100)
        return()=>{
            setFade('') ;
            clearTimeout(timer) ;
        }
    },[tab])  //tab  
    if(tab == 0){
        return <div className={`start ${fade}`}>0</div>
    }else if(tab == 1){
        return <div className={`start ${fade}`}>1</div>
    }else if(tab == 2){
        return <div className={`start ${fade}`}>2</div>
    }
}
export default Detail ;